<template>
  <div class="wrapper">
    <div class="top">Layer</div>
    <div class="content">
      <q-list bordered separator>
        <q-item
          v-for="(item, index) in resources"
          :key="index"
          :active="activeIndex === index"
          clickable
          v-ripple
          active-class="active"
          @click="onClickComponent(index)"
        >
          <div class="division" :class="{ text: item.text }"></div>
          <q-item-section class="item-selection">
            <template v-if="item.text">
              <div class="textbox">
                <div class="left">
                  <q-icon size="24px" name="text_fields" color="gray"></q-icon>
                </div>
                <div class="content">
                  <q-input
                    v-model="item.text"
                    :dense="true"
                    borderless
                    autogrow
                    input-class="textEditor"
                    @update:model-value="(evt) => onChangeTextBox(evt)"
                  />
                </div>
              </div>
            </template>
            <template v-else>
              <div class="imagebox">
                <q-img
                  :src="item._element.currentSrc"
                  fit="contain"
                  spinner-size="32px"
                  spinner-color="gray"
                  style="height: 50px; max-width: 50px"
                />
                <q-file
                  ref="imagePickerRef"
                  v-model="pickerImageFile"
                  accept=".jpg, .png, image/*"
                  style="display: none"
                  @update:model-value="imagePickerHandler(index)"
                ></q-file>
                <div class="right">
                  <q-btn
                    size="xs"
                    style="background: #ff0080; color: white"
                    @click="onClickChangeImage(index)"
                  >
                    <q-icon name="image" size="24px" color="gray"></q-icon>
                  </q-btn>
                </div>
              </div>
            </template>
          </q-item-section>
        </q-item>
      </q-list>
    </div>
  </div>
</template>

<script>
import { ref, computed, watch, onMounted } from "vue";

export default {
  name: "Layer",
  props: {
    componentList: Array,
    activeObjectIndex: Number,
  },
  emit: ["emit_selectObject", "emit_changeTextBox", "emit_changeImage"],
  setup(props, { emit }) {
    let componentList = computed(() => props.componentList);
    let resources = ref(componentList.value);
    let activeIndex = ref(null);
    let imagePickerRef = ref(null);
    let pickerImageFile = ref(null);

    onMounted(() => {});

    watch(props, ({ activeObjectIndex }) => {
      if (activeObjectIndex == -1) {
        activeIndex.value = null;
        return;
      }
      onClickComponent(activeObjectIndex);
    });

    const onClickComponent = (componentIndex) => {
      activeIndex.value = componentIndex;
      emit("emit_selectObject", componentIndex);
    };

    const onChangeTextBox = (text) => {
      emit("emit_changeTextBox", {
        changeIndex: activeIndex.value,
        changeText: text,
      });
    };

    const onClickChangeImage = (index) => {
      imagePickerRef.value[index].pickFiles();
    };

    const imagePickerHandler = (index) => {
      if (pickerImageFile.value) {
        let reader = new FileReader();

        reader.onload = (e) => {
          const image = new Image();
          image.src = e.target.result;
          image.onload = () => {
            // 이미지 리사이즈
            let reImage = resizeImage(image);
            emit("emit_changeImage", {
              componentIndex: index,
              base64Image: reImage,
            });
          };
        };
        reader.readAsDataURL(pickerImageFile.value);
      }
    };

    const resizeImage = (image) => {
      let canvas = document.createElement("canvas");
      let maxSize = 1024;
      let width = image.width;
      let height = image.height;

      if (width > height) {
        if (width > maxSize) {
          height *= maxSize / width;
          width = maxSize;
        }
      } else {
        if (height > maxSize) {
          width *= maxSize / height;
          height = maxSize;
        }
      }

      canvas.width = width;
      canvas.height = height;
      canvas.getContext("2d").drawImage(image, 0, 0, width, height);

      return canvas.toDataURL("image/jpeg");
    };

    return {
      resources,
      activeIndex,
      imagePickerRef,
      pickerImageFile,
      imagePickerHandler,
      onClickComponent,
      onChangeTextBox,
      onClickChangeImage,
    };
  },
};
</script>

<style lang="scss" scoped>
::v-deep(.q-item) {
  padding-left: 0px !important;
  background-color: #fff !important;
}
.wrapper {
  min-width: 30vw;

  .top {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    min-height: 30px;
    color: #fff;
    background-color: #333;
  }

  .content {
    .component {
      display: flex;
      padding: 16px 8px;
      border-bottom: 1px solid #d6d6d6;
      cursor: pointer;

      &:hover {
        background-color: #ffc107 !important;
        color: #fff;
      }
    }

    .active {
      background-color: #ffc107 !important;
      color: #fff;
    }
  }
}

.division {
  width: 4px;
  margin-right: 8px;
  background-color: #ff007f;
  &.text {
    background-color: #f2f2f2;
  }
}

.item-selection {
  .textbox {
    display: flex;

    .left {
      align-self: center;
      padding-left: 12px;
      padding-right: 12px;
    }
    .content {
      width: 100%;
      ::v-deep(.textEditor) {
        background-color: #fff;
        padding: 8px;
        width: 100%;
      }
    }
    .right {
      margin-left: auto;
      align-self: center;
    }
  }

  .imagebox {
    display: flex;

    .right {
      margin-left: auto;
      align-self: center;
    }
  }
}
</style>
